*{
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}
/*pára que dentro deles consiga setar elementos em %*/
html, body {

    height: 100%;
    width: 100%;
}

body {
    font: 400 15px Roboto, sans-serif;
    -webkit-font-smoothing: antialiased; /*melhora a visualização da fonte no google Crhome*/
}

input, button, textarea, select{
    font: 400 15px Roboto, sans-serif;
    border: none;
    padding-top: 10px;
    padding-bottom: 10px;
    color: #828282;
    
}

button {
    cursor: pointer;
}

form input  {
    width: 100%;
    height: 40px;
    border: none;
}

.dashboard header {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    height: 70px;
    background-color:#00DF89;
    padding: 10px 20px;
}

.dashboard header a {
    text-decoration: none;
    color: white;
}

.dashboard header a  > svg {
    color: white;
    font-size: 15px;
}

div.dashboard {
    display: flex;
    flex-direction: row;
    height: 100%;

}

.sidebar {
    display: block;
    height: 100%;
    width: 250px;
    background-color: #ffffff;

}

.sidebar .sidebar-logo {
    display: flex;
    height: 70px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 10px 20px;
    background-color: #00DF89;
}

.sidebar .sidebar-logo img {
    height: 60px;
}



div.menu ul{
    list-style-type: none;
}

div.menu ul li {
    padding: 20px 20px;
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
}
div.menu ul li svg{
    color: #828282;
    font-size: 15px;
}

div.menu ul li a {
    color: #828282;
    margin-left: 10px;
    text-decoration: none;
    text-transform: uppercase;
}

div .conteudo {
    height: 100vh;
    padding-top: 30px;
}

.conteudo .box-eventos{
    max-width: 1000px;
    margin: 0 auto;
    padding: 25px;
    background-color: #ffffff;
    box-shadow:0 0 100px rgb(0, 0, 0, 0.1);
}

.conteudo .box-eventos h1 {
    margin: 0;
    padding: 0;
    font-size: 20px;
    color: #828282;
}

.conteudo .box-eventos p{
    font-size: 12px;
    color: #828282;
}

.conteudo .box-eventos table {
    margin-bottom: 10px;
    margin-top: 10px;
    border: none;
    border-bottom: solid 1px  #dcdce6;
    font: 400 15px Roboto, sans-serif;
    color: #828282;
    
}


.conteudo .box{
    max-width: 1000px;
    margin: 0 auto;
    padding: 25px;
    background-color: #ffffff;
    box-shadow:0 0 100px rgb(0, 0, 0, 0.1);
}

.conteudo .box h1 {
    margin: 0;
    padding: 0;
    font-size: 20px;
    color: #828282;
}

.conteudo .box p{
    font-size: 12px;
    color: #828282;
}

.conteudo .box table {
    margin-bottom: 10px;
    margin-top: 10px;
    border: none;
    border-bottom: solid 1px  #dcdce6;
    font: 400 15px Roboto, sans-serif;
    color: #828282;
    
}

th{
    padding: 5px;
}

td{
    padding: 5px;
}

button{
    padding: 5px;
}